















import { isString } from "lodash";
import PriceDb from "../lib/priceDb/PriceDatabase";

export default {
    props: {
        item: {
            type: [String, Array],
            required: true
        },
        priceDb: {
            type: PriceDb,
            required: true
        }
    },
    computed: {
        isGroup() {
            return !isString(this.item);
        },
        priceValues() {
            return this.isGroup
                ? this.priceDb.getSelection(this.item)
                : this.priceDb.get(this.item);
        }
    }
};
