
























































































































































































































































import { SORTERS } from "../lib/data/sort";
import { FORMATS } from "../lib/data/format";
import { BANLISTS } from "../lib/data/banlist";
import {
    CARD_ATTRIBUTE,
    CARD_LEVEL,
    CARD_LINKARROWS,
    CARD_RACE,
    CARD_SPELL_TYPE,
    CARD_TRAP_TYPE,
    CARD_TYPE
} from "../lib/data/cards";
import search from "../lib/cardDb/search";
import Multiselect from "vue-multiselect";

export default {
    components: { Multiselect },
    props: {
        pairsArr: {
            type: Array,
            required: true
        },
        sets: {
            type: Array,
            required: true
        },
        showAdvancedFilters: {
            type: Boolean,
            required: false,
            default: true
        }
    },
    data: () => {
        return {
            filter: {
                name: "",
                type: {
                    active: "Any",
                    options: CARD_TYPE
                },
                race: {
                    active: "Any",
                    options: CARD_RACE
                },
                attribute: {
                    active: "Any",
                    options: CARD_ATTRIBUTE
                },
                level: {
                    active: "Any",
                    options: CARD_LEVEL
                },
                linkmarkers: {
                    active: "Any",
                    options: CARD_LINKARROWS
                },
                spelltype: {
                    active: "Any",
                    options: CARD_SPELL_TYPE
                },
                traptype: {
                    active: "Any",
                    options: CARD_TRAP_TYPE
                },
                format: {
                    active: FORMATS[0],
                    options: FORMATS
                },
                banlist: {
                    active: BANLISTS[0],
                    options: BANLISTS
                },
                sets: {
                    active: null,
                    options: []
                }
            },
            sort: {
                active: SORTERS[0],
                options: SORTERS
            }
        };
    },
    computed: {
        isMonster() {
            return !["Any", "Spell Card", "Trap Card"].includes(
                this.filter.type.active
            );
        },
        isMonsterLink() {
            return this.filter.type.active === "Link Monster";
        },
        isSpell() {
            return this.filter.type.active === "Spell Card";
        },
        isTrap() {
            return this.filter.type.active === "Trap Card";
        }
    },
    mounted() {
        this.filter.sets.options = this.sets;
        this.filterCards();
    },
    methods: {
        filterCards() {
            const pairsArrFiltered = search(
                this.pairsArr,
                this.filter,
                {
                    monster: this.isMonster,
                    monsterLink: this.isMonsterLink,
                    spell: this.isSpell,
                    trap: this.isTrap
                },
                this.sort.active.fn
            );

            this.$emit("change", pairsArrFiltered, this.filter);
        }
    }
};
